import React from 'react'
import Lottie from 'lottie-react'
import socialmedia from "./animations/Socialanimation.json"
import lead from "./animations/leadgeneration.json"
export default function Fadingcnt2() {
  return (
    
     <div className='row fadingcnt2 gx-0 fontclass' >
        <div className='fadingitem2 col-12 col-md-6 gx-0'data-aos="fade-right"  ><Lottie animationData={socialmedia}></Lottie><div className='fadingitem2div'><h1>Social Media Integration</h1></div></div>
        <div className='fadingitem2 col-12 col-md-6 gx-0'data-aos="fade-left" ><Lottie backgroundColor={"black"} style={{backgroundColor:"black"}} animationData={lead}></Lottie><div className='fadingitem2div'><h1>Targeted Lead Generation</h1></div></div>
     </div>
    
  )
}
